<template>
  <div v-if="labRequest.vaccines" class="card" style="margin-top: 1em;">
    <header class="card-header">
      <p class="card-header-title">Vaccines:</p>
    </header>
    <div class style="margin: 0.5em; padding-bottom: 0.5em; border-bottom: 2px solid #dbdbdb;">
      <v-select
        @input="addVaccine"
        v-model="selected"
        placeholder="Add vaccine+"
        :options="vaccineOptions"
      ></v-select>
    </div>
    <div class="card-content">
      <div class="content">
        <table class="table is-fullwidth" v-if="labRequestHasVaccines">
          <thead>
            <tr>
              <th></th>
              <th>Name</th>
              <th>Select Discount</th>
              <th>Discount</th>
              <th>Subtotal</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(vaccine, key) in labRequest.vaccines" :key="key">
              <td>
                <button class="button is-danger is-outlined">
                  <span class="icon is-small" @click="removeVaccine(vaccine.name)">
                    <i class="far fa-trash-alt"></i>
                  </span>
                </button>
              </td>
              <td>{{ vaccine.name }}</td>
              <td>
                <div class="select">
                  <select @change="addDiscount($event.target.value, vaccine)">
                    <option value selected disabled>Select discount</option>
                    <option
                      :value="null"
                      :disabled="!isVaccineDiscounted(vaccine)"
                      :class="{'has-text-danger': isVaccineDiscounted(vaccine)}"
                    >Remove discount</option>
                    <option
                      v-for="(discount, index) in vaccine.discounts"
                      :key="index"
                      :value="vaccineDiscount(vaccine.price, discount.value)"
                    >{{ discount.name }}: {{ discount.value }}%</option>
                  </select>
                </div>
              </td>
              <td class="has-text-right">{{ totalVaccineDiscount(vaccine) }}</td>
              <td class="has-text-right">{{ totalVaccineCost(vaccine) }}</td>
            </tr>
            <tr>
              <td colspan="4">
                <strong>Subtotal</strong>
              </td>
              <td class="has-text-right">
                <strong>{{ totalCost() }}</strong>
              </td>
            </tr>
            <tr>
              <td colspan="4">
                <strong>Discount</strong>
              </td>
              <td class="has-text-right">
                <strong>{{ totalDiscount() }}</strong>
              </td>
            </tr>
            <tr>
              <td colspan="4">
                <strong>Tax 15%</strong>
              </td>
              <td class="has-text-right">
                <strong>{{ tax() }}</strong>
              </td>
            </tr>
            <tr style="border-top: 2px solid #dbdbdb;">
              <td colspan="4">
                <strong>Total</strong>
              </td>
              <td class="has-text-right">
                <strong class="has-text-link is-size-5">{{ total() }}</strong>
              </td>
            </tr>
          </tbody>
        </table>
        <div v-else>
          <strong>No Vaccine Has Been Selected</strong>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  calulateDiscount,
  calculateTotalPrice,
  calculateTotalCost,
  calculateTax,
  calculateTotalDiscount,
  calculateTotal
} from "../helpers/utility";

export default {
  name: "LabRequestCheckoutVaccineComponent",
  created() {
    let token = this.$store.state.authentication.inMemoryToken;
    this.$store.dispatch("fetchVaccines", token);
  },
  data() {
    return {
      amountPaid: null,
      selected: ""
    };
  },
  methods: {
    addVaccine(vaccine) {
      let labRequest = this.$store.state.cashier.currentCheckoutLabRequest;
      if (vaccine) {
        labRequest.vaccines.push(vaccine.vaccine);
      }
    },
    removeVaccine(name) {
      let index = -1;
      let labRequest = this.$store.state.cashier.currentCheckoutLabRequest;
      for (let i = 0; i < labRequest.vaccines.length; i++) {
        if (labRequest.vaccines[i].name === name) {
          index = i;
        }
      }
      if (index != -1) {
        this.labRequest.vaccines.splice(index, 1);
      }
    },
    addDiscount(discount, vaccine) {
      if (vaccine.active_discounts.length === 0) {
        vaccine.active_discounts.push(discount);
      } else {
        vaccine.active_discounts.pop();
        if (discount !== null) {
          vaccine.active_discounts.push(discount);
        }
      }
    },
    isVaccineDiscounted(vaccine) {
      return vaccine.active_discounts.length > 0;
    },
    vaccineDiscount(price, discountPercentage) {
      return ((discountPercentage / 100) * price).toFixed(2);
    },
    totalVaccineDiscount(vaccine) {
      return calulateDiscount(vaccine);
    },
    totalVaccineCost(vaccine) {
      return (vaccine.price - this.totalVaccineDiscount(vaccine)).toFixed(2);
    },
    tax() {
      return calculateTax(
        15,
        this.$store.state.cashier.currentCheckoutLabRequest
      );
    },
    totalDiscount() {
      return calculateTotalDiscount(
        this.$store.state.cashier.currentCheckoutLabRequest
      );
    },
    totalPrice() {
      return calculateTotalPrice(
        this.$store.state.cashier.currentCheckoutLabRequest
      );
    },
    totalCost() {
      return calculateTotalCost(
        this.$store.state.cashier.currentCheckoutLabRequest
      );
    },
    total() {
      return calculateTotal(
        15,
        this.$store.state.cashier.currentCheckoutLabRequest
      );
    }
  },
  computed: {
    labRequest() {
      return this.$store.state.cashier.currentCheckoutLabRequest;
    },
    labRequestHasVaccines() {
      return (
        this.$store.state.cashier.currentCheckoutLabRequest.vaccines.length > 0
      );
    },
    vaccineOptions() {
      return this.$store.getters.vaccinesToBeOrdered;
    }
  }
};
</script>