<template>
  <div v-if="labRequest.exams" class="card" style="margin-top: 1em;">
    <header class="card-header">
      <p class="card-header-title">Exams:</p>
    </header>
    <div style="margin: 0.5em; padding-bottom: 0.5em; border-bottom: 2px solid #dbdbdb;">
      <v-select @input="addExam" v-model="selected" placeholder="Add exam+" :options="examOptions"></v-select>
    </div>
    <div class="card-content">
      <div class="content">
        <table class="table is-fullwidth is-narrow" v-if="labRequestHasExams">
          <thead>
            <tr>
              <th></th>
              <th>Name</th>
              <th>Select Discount</th>
              <th>Discount</th>
              <th>Subtotal</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(exam, key) in labRequest.exams" :key="key">
              <td>
                <button class="button is-danger is-outlined">
                  <span class="icon is-small" @click="removeExam(exam.name)">
                    <i class="far fa-trash-alt"></i>
                  </span>
                </button>
              </td>
              <td>{{ exam.name }}</td>
              <td>
                <div class="select">
                  <select @change="addDiscount($event.target.value, exam)">
                    <option value selected disabled>Select discount</option>
                    <option
                      :value="null"
                      :disabled="!isExamDiscounted(exam)"
                      :class="{'has-text-danger': isExamDiscounted(exam)}"
                    >Remove discount</option>
                    <option
                      v-for="(discount, index) in exam.discounts"
                      :key="index"
                      :value="examDiscount(exam.price, discount.value)"
                    >{{ discount.name }}: {{ discount.value }}%</option>
                  </select>
                </div>
              </td>
              <td class="has-text-right">{{ totalExamDiscount(exam) }}</td>
              <td class="has-text-right">{{ totalExamCost(exam) }}</td>
            </tr>
            <tr>
              <td colspan="4">
                <strong>Subtotal</strong>
              </td>
              <td class="has-text-right">
                <strong>{{ totalCost() }}</strong>
              </td>
            </tr>
            <tr>
              <td colspan="4">
                <strong>Discount</strong>
              </td>
              <td class="has-text-right">
                <strong>{{ totalDiscount() }}</strong>
              </td>
            </tr>
            <tr>
              <td colspan="4">
                <strong>Tax 15%</strong>
              </td>
              <td class="has-text-right">
                <strong>{{ tax() }}</strong>
              </td>
            </tr>
            <tr style="border-top: 2px solid #dbdbdb;">
              <td colspan="4">
                <strong>Total</strong>
              </td>
              <td class="has-text-right">
                <strong class="has-text-link is-size-5">{{ total() }}</strong>
              </td>
            </tr>
          </tbody>
        </table>
        <div v-else>
          <strong>No Exam Has Been Selected</strong>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  calulateDiscount,
  calculateTotalPrice,
  calculateTotalCost,
  calculateTax,
  calculateTotalDiscount,
  calculateTotal
} from "../helpers/utility";

export default {
  name: "LabRequestCheckoutExamComponent",
  created() {
    let token = this.$store.state.authentication.inMemoryToken;
    this.$store.dispatch("fetchExams", token);
  },
  data() {
    return {
      amountPaid: null,
      selected: ""
    };
  },
  methods: {
    addExam(exam) {
      let labRequest = this.$store.state.cashier.currentCheckoutLabRequest;
      if (labRequest.exams && exam) {
        labRequest.exams.push(exam.exam);
      }
    },
    removeExam(name) {
      let index = -1;
      let labRequest = this.$store.state.cashier.currentCheckoutLabRequest;
      for (let i = 0; i < labRequest.exams.length; i++) {
        if (labRequest.exams[i].name === name) {
          index = i;
        }
      }
      if (index != -1) {
        labRequest.exams.splice(index, 1);
      }
    },
    addDiscount(discount, exam) {
      if (exam.active_discounts.length === 0) {
        exam.active_discounts.push(discount);
      } else {
        exam.active_discounts.pop();
        if (discount !== null) {
          exam.active_discounts.push(discount);
        }
      }
    },
    isExamDiscounted(exam) {
      return exam.active_discounts.length > 0;
    },
    examDiscount(price, discountPercentage) {
      return ((discountPercentage / 100) * price).toFixed(2);
    },
    totalExamDiscount(exam) {
      return calulateDiscount(exam);
    },
    totalExamCost(exam) {
      return exam.price - this.totalExamDiscount(exam);
    },
    tax() {
      return calculateTax(
        15,
        this.$store.state.cashier.currentCheckoutLabRequest
      );
    },
    totalDiscount() {
      return calculateTotalDiscount(
        this.$store.state.cashier.currentCheckoutLabRequest
      );
    },
    totalPrice() {
      return calculateTotalPrice(
        this.$store.state.cashier.currentCheckoutLabRequest
      );
    },
    totalCost() {
      return calculateTotalCost(
        this.$store.state.cashier.currentCheckoutLabRequest
      );
    },
    total() {
      return calculateTotal(
        15,
        this.$store.state.cashier.currentCheckoutLabRequest
      );
    }
  },
  computed: {
    examOptions() {
      return this.$store.getters.examsToBeOrdered;
    },
    labRequest() {
      return this.$store.state.cashier.currentCheckoutLabRequest;
    },
    labRequestHasExams() {
      return (
        this.$store.state.cashier.currentCheckoutLabRequest.exams.length > 0
      );
    }
  }
};
</script>