import uuidv4 from "uuid/v4";

const generateID = function() {
  return uuidv4();
};

const calulateDiscount = function(item) {
  if (item.active_discounts.length <= 0) {
    return 0;
  }
  return item.active_discounts.reduce((total, discount) => total + discount, 0);
};

const calculateTotalDiscount = function(labRequest) {
  let items = labRequest.exams ? labRequest.exams : labRequest.vaccines;
  if (items) {
    return items
      .reduce((total, item) => total + parseFloat(calulateDiscount(item)), 0)
      .toFixed(2);
  } else {
    return 0;
  }
};

const calculateTotalPrice = function(labRequest) {
  let items = labRequest.exams ? labRequest.exams : labRequest.vaccines;
  if (items) {
    return items.reduce((total, item) => total + item.price, 0);
  } else {
    return 0;
  }
};

const calculateTotalCost = function(labRequest) {
  return (
    calculateTotalPrice(labRequest) - calculateTotalDiscount(labRequest)
  ).toFixed(2);
};

const calculateTax = function(taxPercentage, labRequest) {
  return ((taxPercentage / 100) * calculateTotalCost(labRequest)).toFixed(2);
};

const calculateTotal = function(taxPercentage, labRequest) {
  return (
    parseFloat(calculateTotalCost(labRequest)) +
    parseFloat(calculateTax(taxPercentage, labRequest))
  ).toFixed(2);
};

export {
  generateID,
  calulateDiscount,
  calculateTotalPrice,
  calculateTotalCost,
  calculateTax,
  calculateTotalDiscount,
  calculateTotal
};
