<template>
  <div class="modal" :class="{'is-active': active}" v-if="labRequest">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p
          class="modal-card-title"
        >Patient: {{ labRequest.patient.first_name + ' ' + labRequest.patient.last_name}}</p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div class="box">
          <div class="field has-addons has-addons-centered">
            <p class="control is-link">
              <span class="select">
                <select>
                  <option>$</option>
                </select>
              </span>
            </p>
            <p class="control is-expanded">
              <input
                class="input is-link"
                v-model.number="amountPaid"
                type="text"
                placeholder="Amount"
              />
            </p>
          </div>
          <div>
            <div class="level">
              <div class="level-left">
                <div class="level-item">
                  <p class="is-size-5">Total:</p>
                </div>
              </div>
              <div class="level-right">
                <div class="level-item">
                  <p class="is-size-5 has-text-right">{{ total(labRequest) }}</p>
                </div>
              </div>
            </div>
            <div class="level">
              <div class="level-left">
                <div class="level-item">
                  <p class="is-size-5">Balance:</p>
                </div>
              </div>
              <div class="level-right">
                <div class="level-item">
                  <p class="is-size-5 has-text-right">{{ balance(labRequest) }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <labrequestcheckoutexam-component></labrequestcheckoutexam-component>
        <labrequestcheckoutvaccine-component></labrequestcheckoutvaccine-component>
        <div class="field is-grouped" style="margin-top: 1em;">
          <div class="control is-expanded">
            <button
              class="button is-link is-fullwidth is-medium"
              @click="confirmPayment(labRequest)"
              :disabled="!validatePaymentAmount(labRequest)"
            >
              <span class="icon is-small">
                <i class="fas fa-check"></i>
              </span>
              <span>Confirm</span>
            </button>
          </div>
          <div class="control is-expanded">
            <button
              class="button is-danger is-fullwidth is-medium is-outlined"
              @click="confirmPayment(labRequest)"
            >
              <span class="icon is-small">
                <i class="far fa-trash-alt"></i>
              </span>
              <span>Delete</span>
            </button>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import { generateID, calculateTotal } from "../helpers/utility";
import LabRequestCheckoutExamComponent from "@/components/LabRequestCheckoutExamComponent";
import LabRequestCheckoutVaccineComponent from "@/components/LabRequestCheckoutVaccineComponent";
export default {
  created() {
    let token = this.$store.state.authentication.inMemoryToken;
    this.$store.dispatch("fetchExams", token);
    this.$store.dispatch("fetchVaccines", token);
  },
  name: "LabRequestCheckoutComponent",
  data() {
    return {
      amountPaid: null,
      selected: ""
    };
  },
  methods: {
    validatePaymentAmount(labRequest) {
      return this.amountPaid != null && this.balance(labRequest) >= 0;
    },
    balance(labRequest) {
      return (this.amountPaid
        ? this.amountPaid - calculateTotal(15, labRequest)
        : 0).toFixed(2);
    },
    total(labRequest) {
      return calculateTotal(15, labRequest);
    },
    closeModal() {
      this.$store.dispatch("toggleLabRequestCheckoutModalOn", false);
      this.$store.dispatch("updateCurrentCheckoutLabRequest", null);
      this.amountPaid = null;
    },
    confirmPayment(labRequest) {
      labRequest.status = "paid";
      labRequest.users.push(this.$store.state.authentication.currentUser);
      labRequest.updated_at = Date.now();
      this.generateTransaction(labRequest);
      this.$socket.emit("update-labrequest", labRequest);
      this.closeModal();
    },
    generateTransaction(labRequest) {
      let transaction = {
        lab_request: labRequest.key,
        users: [this.$store.state.authentication.currentUser],
        patient: labRequest.patient,
        total_cost: calculateTotal(15, labRequest),
        amount_paid: this.amountPaid,
        payment_method: "cash",
        balance: this.balance(labRequest),
        status: "paid",
        key: generateID(),
        type: "transaction",
        created_at: Date.now(),
        updated_at: Date.now()
      };
      this.$socket.emit("create-transaction", transaction);
    }
  },
  computed: {
    active() {
      return this.$store.state.cashier.isLabRequestCheckoutModalOn;
    },
    labRequest() {
      return this.$store.state.cashier.currentCheckoutLabRequest;
    }
  },
  components: {
    "labrequestcheckoutexam-component": LabRequestCheckoutExamComponent,
    "labrequestcheckoutvaccine-component": LabRequestCheckoutVaccineComponent
  }
};
</script>
<style scoped>
@import "../../node_modules/vue-select/dist/vue-select.css";
</style>